import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import about from '@/views/about'
import contact from '@/views/contact'
import cases from '@/views/cases'
import solutions from '@/views/solutions'
import culture from '@/views/culture'
import structure from '@/views/structure'
import certificate from '@/views/certificate'
import partners from '@/views/partners'


Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'index',
            component: index
        },
        {
            path: '/index',
            name: 'index',
            component: index
        }, {
            path: '/partners',
            name: 'partners',
            component: partners
        }, {
            path: '/certificate',
            name: 'certificate',
            component: certificate
        }, {
            path: '/structure',
            name: 'structure',
            component: structure
        },
        {
            path: '/culture',
            name: 'culture',
            component: culture
        },
        {
            path: '/about',
            name: 'about',
            component: about
        }, {
            path: '/contact',
            name: 'contact',
            component: contact
        }, {
            path: '/cases',
            name: 'cases',
            component: cases
        }, {
            path: '/solutions',
            name: 'solutions',
            component: solutions
        }
    ]
})


