.bounce-leave-active {
  /* animation: bounce-in .5s inherit; */
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.topbar {
  height: 40px;
  line-height: 40px;
  background-color: #2a3648;
}

.topbar div {
