  width: 1000px;
}

.topbar div a {
  float: right;
  color: #bfcbd9;
  text-decoration: none;
  font-size: 13px;
  margin-left: 10px;
}

.topbar div a i {
  margin-right: 5px;
}

.nav {
  background-color: #324157;
}

.cimg {
  float: left!important;
  line-height: 60px;
  height: 60px;
}

.cimg img {
  vertical-align: middle;
}

#app .el-menu {
